import './App.css';
import React, { Component } from 'react';
export default class TableRow extends Component {
    editUser= () => {
        this.props.changeState()
        this.props.editUser() 
    }
    deleteUser = (idUser) => {
        this.props.deleteUser(idUser);
    }
    render() {
        return (
                <tr>
                    <td>{this.props.stt}</td>
                    <td>{this.props.userName}</td>
                    <td>{this.props.phone}</td>
                    <td>{this.props.permission}</td>
                    <td><div className="btn btn-success" id="btn1" onClick={() => this.editUser()}>Sửa</div></td>
                    <td><div className="btn btn-danger" id="btn2" onClick={() => this.deleteUser(this.props.stt)}>Xóa</div></td>
                </tr>
        );
    }
}
