import './App.css';
import React, { Component } from 'react'
export default class editUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id:this.props.userObject.id,
            Name : "",
            Phone : "",
            Permission : ""
        }
    }
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]:value
        })
    }
    saveButton = () => {
        var info ={};
        info.id = this.props.userObject.id;
        info.Name = this.state.Name;
        info.Phone = this.state.Phone;
        info.Permission = this.state.Permission;
        this.props.getInfo(info);
        this.props.changeState();
        console.log(info.id);
    }
    render() {
        return (
            <div>
                 <div className="form-group" id="them">
                    <form>
                    <label htmlFor="Ten" />
                        <input type="text" name="Name" onChange={(event)=>this.isChange(event)}className="form-control" placeholder="Name" defaultValue={this.props.userObject.Name}/>
                        <label htmlFor="So Dien Thoai" />
                        <input type="text" name="Phone" onChange={(event)=>this.isChange(event)} className="form-control" placeholder="Phone" defaultValue={this.props.userObject.Phone}/>
                        <input type="text"  name="Permission" onChange={(event)=>this.isChange(event)} className="form-control" placeholder="ADMIN" defaultValue={this.props.userObject.Permission}/>
                        <input type="reset" value="Edit" onClick={()=>this.saveButton()}/>
                    </form>
                </div>
            </div>
        );
    }
}
