import './App.css';
import React, { Component } from 'react';
export default class AddUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id:"",
            Name:"",
            Phone:"",
            Permission:""
        }
    }
    StartBtn = () =>{
        this.setState({
            id:"",
            Name:"",
            Phone:"",
            Permission:""
        })
    }
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name]:value
        })
        var item = [];
        item.id = this.props.id;
        item.Name = this.props.Name;
        item.Phone = this.props.Phone;
        item.Permission = this.props.Permission;
        console.log(item);
        // this.setState({
        //     temp:event.target.value
        // });
    }
    hienthiNut = () => {
        if(this.props.thaydoi===true){
            return(
                <div className="form-group" id="them">
                    <form>
                    <label htmlFor="Ten" />
                        <input type="text" name="Name"className="form-control" placeholder="Name" onChange={(event) =>this.isChange(event)}/>
                        <label htmlFor="So Dien Thoai" />
                        <input type="text" name="Phone"className="form-control" placeholder="Phone" onChange={(event) =>this.isChange(event)} />
                        <label htmlFor="Quyen" />
                        <input type="text"  name="Permission" className="form-control" placeholder="ADMIN" onChange={(event) =>this.isChange(event)} />
                        <input type="reset" className="btn btn-primary" onClick={(Name,Phone,Permission)=>{this.props.add(this.state.Name,this.state.Phone,this.state.Permission)}} value="Submit"/>
                    </form>
                </div>
            )
        } 
    }
    render() {
        return (
            
            <div>
                 {
                     this.hienthiNut()
                 }
            </div>
        );
    }
}
