
import './App.css';
import React, { Component } from 'react';
import Header from './Header.js';
import AddUser from './AddUser.js';
import Table from './Table.js';
import Search from './Search'; 
import Data from './Data.json';
const { v4: uuidv4 } = require('uuid');
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
        thaydoi:false,
        data:[],
        searchText:'',
        editUsers:true,
        userObject:{}
    }
}

componentWillMount() {
  if(localStorage.getItem("userData")===null){
    localStorage.setItem('userData',JSON.stringify(Data));
  } else{
    var temp = JSON.parse(localStorage.getItem('userData'));
    this.setState({
      data:temp
    })
  }
}

changeState = () => {
  this.setState({
    editUsers:!this.state.editUsers
  })
}
deleteUser = (idUser) => {
  console.log(idUser);
  var tempData = this.state.data;
  tempData = tempData.filter(item => item.id !== idUser);
  this.setState({
    data:tempData
  })
  localStorage.setItem('userData',JSON.stringify(tempData));
}
addNews = (Name,Phone,Permission) => {
  var item = {};
  item.id = uuidv4();;
  item.Name = Name;
  item.Phone = Phone;
  item.Permission = Permission;
  var items = this.state.data;
  items.push(item);
  this.setState({
    data:items
    }
  )
  localStorage.setItem('userData',JSON.stringify(this.state.data));  
}
checkConnect = (dl) => {
  this.setState({
    searchText:dl
  })
}
  getEditInfo= (info) =>{
    console.log(info.Name);
    this.state.data.forEach((value,key) =>{
      if(value.id===info.id){
        console.log("trung roi nhe");
        value.Name = info.Name;
        value.Phone = info.Phone;
        value.Permission = info.Permission;
      }
    })
    localStorage.setItem('userData',JSON.stringify(this.state.data));
  }
  thongBao = () => {
    alert("hello world")
  }
  doitrangthai = () => {
    this.setState({
      thaydoi: !this.state.thaydoi
    })
  }
  editUser = (user) => {
    this.setState({
      userObject:user
    })
  } 
  render() {
    var ketqua =[];
    localStorage.setItem('userData',JSON.stringify(this.state.data));
        this.state.data.forEach((item)=>{
            if(item.Name.toString().toLowerCase().indexOf(this.state.searchText) !==-1){
              ketqua.push(item);
            }
        })
    return (
      <div>
          <Header></Header>
          <div className="container">
            <Search getEditInfo={(info)=>this.getEditInfo(info)}editUser ={(user)=>this.editUser(user)} userObject={this.state.userObject}changeState={()=>this.changeState()} editUsers={this.state.editUsers} find={(dl)=>this.checkConnect(dl)} ketnoi={()=> this.doitrangthai()} thaydoi={this.state.thaydoi}></Search>
            <Table deleteUser={(idUser)=> this.deleteUser(idUser)} editUser ={(user)=>this.editUser(user)} changeState={()=>this.changeState()}dataUser={ketqua}></Table>
            <AddUser add={(Name,Phone,Permission)=>{this.addNews(Name,Phone,Permission)}} thaydoi={this.state.thaydoi}></AddUser>
          </div>
      </div>
    );
  }
}
export default App;


