import './App.css';
import React, { Component } from 'react';
import EditUser from './editUser.js';
export default class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            temp:'',
            userObjectsss:{}
        }
    }
    isShow = () => {
        if(this.props.editUsers ===true){
            return <EditUser getInfo = {(event)=>this.getInfo(event)} userObject={this.props.userObject} changeState={() => this.props.changeState()}></EditUser>
        }
    }
    getInfo = (event) => {
        this.setState({
            userObjectsss:event
        })
        console.log(event)
        this.props.getEditInfo(event);
    }
    isChange = (event) => {
        console.log(event.target.value);
        this.setState({
            temp:event.target.value
        });
    }
    hienthinut = () => {
        if(this.props.thaydoi === true){
            return <div className="btn btn-success nut1" onClick={()=>this.props.ketnoi()}>CLOSE</div>
        }
        else {
            return <div className="btn btn-info nut1" onClick={(dl)=>this.props.ketnoi()}>ADDNEW</div>
        }
    }

    render() {
        
        return (
            <div>
                {
                    this.isShow()
                }
                <div className="row">
                    <div className="form-group">
                    <label/>
                    <input type="text" className="form-control" onChange={(event) =>this.isChange(event)} name="search" placeholder="Nhập từ khóa tìm kiếm" />
                    </div>
                    <button type="button" className="nut" onClick={() => this.props.find(this.state.temp)}>FIND</button>
                </div>
                <div className="btn-group">
                    
                    {
                        this.hienthinut()
                    }
                </div>    
            </div>
        );
    }
}
