import './App.css';
import React, { Component } from 'react';
import TableRow from './TableRow.js';
export default class Table extends Component {
    deleteUser = (idUser) => {
        this.props.deleteUser(idUser);
    }
    mapData = () => this.props.dataUser.map((value,key) =>  (
    <TableRow key={value.id}
    changeState={()=>this.props.changeState()} 
    editUser={(user)=>this.props.editUser(value)}
    stt={value.id}  
    data={value} 
    deleteUser = {(idUser) =>this.deleteUser(idUser)}
    userName={value.Name}  
    phone={value.Phone} 
    permission={value.Permission}></TableRow>))
    render() {
        return (
                <table id="table-one" className="table table-striped table-inverse table-responsive">
                    <thead className="thead-inverse">
                        <tr>
                            <th>STT</th>
                            <th>TÊN</th>
                            <th>SỐ ĐIỆN THOẠI</th>
                            <th>QUYỀN</th>
                            <th>EDIT</th>
                            <th>DELETE</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                          this.mapData()
                        }
                    </tbody>
                    <tfoot></tfoot>
                </table>
        );
    }
}
